@font-face {
  font-family: 'FineCollege';
  src: url('./FineCollege.ttf') format('truetype');
}
@font-face {
  font-family: 'CubbyBrush';
  src: url('./CubbyBrush.ttf') format('truetype');
}
@font-face {
  font-family: 'SaucerBB';
  src: url('./SaucerBB.ttf') format('truetype');
}

.calculation {
  width: fit-content;
  margin: 8px;
  display: flex;
  flex-direction: column;
  border: 2px dotted blue;
  padding: 8px;
}

.operator {
  height: 0px;
  color: #0051f5;
  font-size: 100px;
  display: flex;
  align-items: center;
}

.digit {
  min-width: 60px;
  max-width: 60px;
  height: 80px;
  background: #0051f5;
  color: #fae712;
  cursor: move;
  font-size: 100px;
  border: 1px dashed grey;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 8px;
  padding: 12px;
}

.digit-panel {
  min-width: 60px;
  max-width: 60px;
  height: 80px;
  border: 1px dashed red;
  border-radius: 20px;
  margin: 8px;
  padding: 12px;
}
